*{
  margin: 0;
  padding: 0;
}
.navbar{
  margin: 0 6%;
}
.logo{
  font-weight: 600 ;
  font-size: 23px;
}
.worldspan{
  color: green;
}
.searchbar input{
  margin-left: 15px;
  width: 300px;
  outline: none;
  border: none;
  padding: 8px;
  background-color: #80808000;
}

.searchbar{
  margin-top: -10px;
  width: 400px;
  background-color: rgba(128, 128, 128, 0.158);
  border-radius: 25px;
}
.createaccount{
  text-decoration: none;
  color: black;
  cursor: pointer;
  font-size: 19px;
}
.createaccount:hover{
  color: black;
}
.freespan{
  color: blue;
}
.searchicon{
  cursor: pointer;
  margin-left: 10px;
  height: 25px;
  width: 25px;
}

@media (max-width:647px) {
  .searchbar input{
    width: auto;
  }
  .searchbar{
    width: auto;
  }
}
@media (max-width:435px) {
  .searchbar input{
    width: 100px;
  }
  .logo{
    font-size: 17px;
  }
  .createaccount{
    font-size: 15px;
  }
}

.imgmidsec img{
  width: 100%;
  height: 500px;
}
.midsecheading{
  color: aliceblue;
  margin-top: 20%;
  margin-left: 10%;
}
.midsech1{
  font-size: 40px;
  font-weight: 600;
}
.midsecp{
  color: rgba(250, 249, 248, 0.774);
  margin-top: -10px;
  font-size: 17px;
}



.signupcontainer{

  max-width: 100%;
  margin: auto;
}
.signupcard{
  max-width: 100%;
}
.signupspan{
  background-color: rgb(201, 243, 202);
  color: rgb(3, 99, 51);
  text-align: center;
}
.createaccountbtn{
  
  text-align: center;
}
.createaccbtn{
  background: rgb(10, 112, 228);border-radius: 22px;width: 100%;color: white;
}
.signupfacbtn {
  border-radius: 5px; border:1px solid rgb(110, 103, 103);width: 100%
}
.signupgogbtn{
  border-radius: 5px; border:1px solid rgb(110, 103, 103);width: 100%
}
.alreadyaccount{
  justify-content: flex-end;margin-right: 5%;font-size: 15px;max-width: 100%;
}
.alreadyaccount a{
  text-decoration: none;margin-left: 1px;font-weight: bold;
}
.imgcontainer img{
  max-width: 100%;margin-top: 8%;min-height: 280px;
}
@media (max-width:770px) {
  .imgcontainer img{
    display: none;
  }
}
.termscond{
  font-size:8pt;text-align: center;margin-top: 4%;color: rgb(146, 149, 153);
}


/* card */
.cardimage img{
  height: 50px;
  width: 50px;
  border-radius: 50px;
}
@media (max-width:707px) {
  .cardfooter{
    display: none;
  }
  
}
@media (max-width:550px) {
  .card01footer{
   display: none;
  }
}
.cardtextimg{
  margin-bottom: 20px;
}
.shareicon{
  background-color: rgba(77, 85, 85, 0.329);
  height: 30px;
  width: 30px;
  margin-left: 20px;
  margin-right: 10px;
  border-radius: 7px;
  
}
